import {
	mapState
} from 'vuex';
export default {
	data() {
		return {
			password: "",
			newPassword: "",
			rPassword: ""
		}
	},
	computed: mapState(['userInfo']),
	methods: {
		Submit: function(e) {
			//验证数据
			if (this.password.length < 6 || this.newPassword.length < 6) {
				uni.showToast({
					icon: 'none',
					title: '密码最短为 6 个字符'
				});
				return;
			}
			if (this.newPassword !== this.rPassword) {
				uni.showToast({
					icon: 'none',
					title: '重复密码不一致'
				});
				return;
			}
			//提交数据
			uni.showLoading({
				title: '提交中',
				mask: false
			});
			global.$http.post('/core/user/doModify', {
				params: {
					username: this.userInfo.user.username,
					password: this.password,
					newPassword: this.newPassword
				},
			}).then(res => {
				uni.hideLoading();
				if (res.status === "0") {
					uni.showToast({
						icon: 'none',
						title: '提交成功'
					});
					//更新密码
					uni.setStorageSync("userIdentity", {username: this.userInfo.user.username, password: this.newPassword});
					uni.navigateBack();
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			}).catch(err => {
				uni.hideLoading();
				uni.showToast({
					title: err.message,
					icon: 'none'
				});
			});
		}
	}
}
